import React ,{useRef, useState} from "react";
import { Canvas, useFrame, useThree} from "@react-three/fiber"
import { Stars } from "@react-three/drei"
import "./Styles/style.scss";
import Space from "./Media/space.jpg"
import MoonImage from "./Media/moon.jpg"
import Normal from "./Media/normal.jpg"
import SelfImage from "./Media/kep.jpg"
import * as THREE from "three"
import Linkedin from "./Media/linkedin.png"
import Github from "./Media/gh.svg"
import CV from "./Media/cv.png"
import CVPDF from "./Media/CV_kserdult.pdf"

let moonX = 0;
let moonY = 0;
let moonZ = 0;
let boxY = 0;
let boxZ = 0;

function Box(){
  const mesh= useRef();
  useFrame(()=> {
    mesh.current.rotation.z=boxZ;
    mesh.current.rotation.y=boxY;
  })
  const texture = new THREE.TextureLoader().load(SelfImage);
  return(
    <mesh position={[0,0,0]} ref={mesh}>
      <boxBufferGeometry attach="geometry" args={[3,3,3]}/>
      <meshStandardMaterial attach="material" map={texture}/>
    </mesh>
  )
}
function Plane({position, map, args, link,num}){
  const mesh= useRef();
  const [hovered, setHovered] = useState(false);
  useFrame(()=> {
    mesh.current.rotation.x+=0.001*num;
    mesh.current.rotation.y+=0.005;
    mesh.current.rotation.z+=0.001*num;
  })
  const handleRoute=()=>{
    window.open(link, "_blank");
  }
  const texture = new THREE.TextureLoader().load(map);
  return(
    <mesh position={position} ref={mesh} onClick={handleRoute} onPointerOver={(event) => setHovered(true)} onPointerOut={(event) => setHovered(false)}>
      <boxBufferGeometry attach="geometry" args={hovered ? [3,3,3] : args}/>
      <meshStandardMaterial attach="material" map={texture} />
    </mesh>
  )
}

function Torus() {
  const mesh=useRef();
  useFrame(()=>{
    mesh.current.rotation.x+=0.01;
    mesh.current.rotation.y+=0.005;
    mesh.current.rotation.z+=0.01;
  })
  return (
    <mesh ref={mesh}>
      <torusBufferGeometry attach="geometry" args={[10,3,16,100]} />
      <meshStandardMaterial attach="material" color={0xff6347 } />
    </mesh>
  );
}

function Background() {
  const {scene} = useThree();
  const spaceTexture = new THREE.TextureLoader().load(Space);
  scene.background=spaceTexture;
  return null;
}

function Moon(){
  const mesh= useRef();
  useFrame(()=>{
    mesh.current.rotation.x=moonX;
    mesh.current.rotation.y=moonY;
    mesh.current.rotation.z=moonZ;
  })
  const moonTexture = new THREE.TextureLoader().load(MoonImage);
  const normalTexture = new THREE.TextureLoader().load(Normal);
  return(
    <mesh position={[-10,0,30]} ref={mesh}>
      <sphereBufferGeometry attach="geometry" args={[3,32,32]} />
      <meshStandardMaterial attach="material" map={moonTexture} normalMap={normalTexture} />
    </mesh>
  );
}
function moveCamera(){
  moonX+=0.025;
  moonY+=0.0375;
  moonZ+=0.025;
  boxY+=0.01;
  boxZ+=0.01;
}
document.body.onscroll=moveCamera

function MovingTheCamera() {
  useFrame((state) => {
    state.camera.position.z=(document.body.getBoundingClientRect().top*-0.01)+10;
    state.camera.position.x=(document.body.getBoundingClientRect().top*-0.0002)-3;
    state.camera.rotation.y=document.body.getBoundingClientRect().top*-0.0002;
    state.camera.updateProjectionMatrix()
  })
  return null
}

export default function App (){
  return(
    <Canvas perspective camera={{ fov: 75, near:0.1, far: 1000}} className="canvas">
      <Stars />
      <ambientLight intensity={0.5} />
      <pointLight position={[5, 5, 5]} angle={0.3}/>
        <Moon />
        <Torus />
        <Box />
        <Plane position={[-4,-5,28]} map={Linkedin} args={[2,2,2]} num={-1.2} link={"https://www.linkedin.com/in/k%C3%A1roly-serd%C3%BClt-1b998720a/"}/>
        <Plane position={[-8,-4.6,32]} map={Github} args={[2,2,2]} num={1.4} link={"https://github.com/kserdult"}/>
        <Plane position={[-13,-4.4,36]} map={CV} args={[2,2,2]} num={-1.3} link={CVPDF}/>
        <Background />
      <MovingTheCamera />
    </Canvas>
  );
}